body{margin:0;padding:0;box-sizing:border-box;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-family:Dosis, Verdana, Geneva, Tahoma, sans-serif;font-size:14px;text-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075);background-color:#f7f8fa}.nav.nav-wrapper,#myFooter{background-color:red}.nav.nav-wrapper{font-family:'Aladin', cursive}.nav.nav-wrapper .brand-logo{font-size:270%}.nav.nav-wrapper li a{font-size:150%}#myWrapper{min-height:100vh;margin-bottom:-120px}#myWrapper::after,#myFooter{content:"";display:block;height:120px;overflow:hidden}.sidenav-trigger{position:relative;top:10px;cursor:pointer}.sidenav-trigger>div{width:30px;height:3px;margin:7px;background-color:#fff;border-radius:30px}.input-field input[type=text]:focus+label{color:red !important}.input-field input[type=text]:focus{border-bottom:1px solid red !important;box-shadow:0 1px 0 0 red !important}.prefix.active{color:red !important}

#myFooterCopyright{font-size:90%;font-family:Dosis;color:#fff}#myDeveloper{font-family:Dosis}.mySocialIconsHolder{display:flex;justify-content:center;align-items:center}.mySocialIcon{display:flex;align-items:center;justify-content:center;text-decoration:none;font-size:160%;margin:5px;width:35px;height:35px;border-radius:30%;color:#3498db;background:#fff;overflow:hidden;position:relative;transition:0.2s linear}.mySocialIcon::before{content:"";display:block;width:120%;height:120%;transform:rotate(-45deg);background:#3498db;position:absolute;top:90%;left:-110%}.mySocialIcon:hover i{color:#fff;transform:scale(1.3)}.mySocialIcon:hover::before{-webkit-animation:aaa 0.7s 1;animation:aaa 0.7s 1;top:-10%;left:-10%}@-webkit-keyframes aaa{0%{top:90%;left:-110%}50%{top:-30%;left:10%}100%{top:-10%;left:-10%}}@keyframes aaa{0%{top:90%;left:-110%}50%{top:-30%;left:10%}100%{top:-10%;left:-10%}}

.myHomeSectionTitleHolder{position:relative}.myHomeSectionTitleHolder .myHomeSectionTitle{font-size:700%;font-weight:800;color:#f00;text-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075)}@media screen and (max-width: 800px){.myHomeSectionTitleHolder .myHomeSectionTitle{font-size:400%;font-weight:800}}#myBackToTopButton{width:70px;height:70px;border-radius:50%;background-color:red;display:none;justify-content:center;align-items:center;border:7px solid #f7f8fa;color:#fff;font-size:200%;position:fixed;bottom:40px;right:40px}@media screen and (max-width: 700px){#myBackToTopButton{width:60px;height:60px;font-size:190%;bottom:20px;right:20px}}

#myLanding{margin:0;height:calc(100vh - 56px);display:flex;justify-content:center;align-items:center}#myLanding #myLandingIcon{width:400px;height:400px;border-radius:50%;box-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075);background:url("/SilvenLEAF.jpg") center/cover}#myLanding #myLandingTitleHolder{width:550px}#myLanding #myLandingTitleHolder h5{margin:0}#myLanding #myLandingTitleHolder h1{margin:0;font-family:'Ranchers', Dosis, sans-serif;color:#f00;text-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075)}#myLanding #myLandingTitleHolder h3{margin:0;font-size:200%;text-align:center}#myLandingButtonHolder{margin-top:50px;text-align:center}#myLandingButtonHolder #myLandingButton{padding:15px 30px;border-radius:30px;color:#fff;font-size:1.6em;border:3px solid white;cursor:pointer;box-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075);background-color:red}

#myImageLeftProjectHolder .myProjectImageHolder{text-align:right}#myImageLeftProjectHolder .myProjectImageHolder .my3dProjectImage{position:relative;left:25px;transform:translateZ(50px) rotateZ(17deg)}#myImageLeftProjectHolder .myProjectButtons button{margin:0px 20px 20px 0px}#myImageLeftProjectHolder ul.myUsedTechnology li{margin-right:15px}#myImageLeftProjectHolder .myProjectDescriptionHolder{margin-left:20px;text-align:left}#myImageRightProjectHolder{flex-flow:row-reverse}#myImageRightProjectHolder .myProjectImageHolder{text-align:left}#myImageRightProjectHolder .myProjectImageHolder .my3dProjectImage{position:relative;left:25px;transform:translateZ(50px) rotateZ(17deg)}#myImageRightProjectHolder .myProjectButtons button{margin:0px 0px 20px 20px}#myImageRightProjectHolder ul.myUsedTechnology li{margin-left:15px}#myImageRightProjectHolder .myProjectDescriptionHolder{margin-right:20px;text-align:right}.myProjectHolder{margin:0px 20px 120px 20px;width:100%;display:flex;justify-content:center;align-items:center}@media screen and (max-width: 800px){.myProjectHolder{margin-left:0;margin-right:0;flex-flow:column !important}.myProjectHolder .myProjectImageHolder{display:flex;justify-content:center;text-align:center;margin:0px}.myProjectHolder .myProjectDescriptionHolder{text-align:center !important}}.myProjectHolder .myProjectImageHolder{perspective:800px;width:300px}.myProjectHolder .myProjectImageHolder img{margin:0;height:200px}.myProjectHolder .myProjectButtons button{padding:10px 20px;border-radius:30px;color:#fff;font-size:18px;border:none;cursor:pointer;background-color:red}@media screen and (max-width: 390px){.myProjectHolder .myProjectButtons button{margin-left:0px !important;margin-right:10px !important}}@media screen and (max-width: 450px){.myProjectHolder .myProjectButtons button.mySeeLiveButton{margin-bottom:10px !important}}.myProjectHolder ul.myUsedTechnology{margin:0;font-family:Dosis;font-weight:800}.myProjectHolder ul.myUsedTechnology li{display:inline-block}.myProjectHolder .myProjectDescriptionHolder{width:auto}.myProjectHolder .myProjectDescription{margin:0;margin-top:10px;font-size:125%}

#mySkills{margin-bottom:50px;display:flex;justify-content:center;flex-wrap:wrap}.mySkillSectionHolder{margin:100px 20px 50px 0px;padding:1.5rem 3rem;width:250px;background-color:#fff;box-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075);position:relative}@media screen and (max-width: 800px){.mySkillSectionHolder{margin:50px auto;display:block;width:70vw}}.mySkillSectionHolder .mySkillSectionIcon{background:red;width:80px;height:80px;border-radius:50%;padding-top:1.25rem;position:absolute;top:-40px;left:50%;margin-left:-40px;text-align:center}.mySkillSectionHolder .mySkillSectionIcon img{width:40px;height:40px;-webkit-filter:invert(100%);filter:invert(100%)}.mySkillSectionHolder .mySkillSectionTitle{margin-top:3rem;margin-bottom:1rem;text-align:center;font-weight:500;line-height:1.2}.mySkillSectionHolder ul li{margin:0px 0px 10px 0px;font-family:'Roboto', sans-serif;color:#58677c}.mySkillSectionHolder ul li img{margin-right:5px;width:20px;height:20px;position:relative;top:5px}

.myContactMeTitleHolder{margin-bottom:50px}#myContact .row{margin:70px}@media screen and (max-width: 800px){#myContact .row{margin-top:0px;margin-bottom:0px}}#myContact .myContactSection{height:200px;background-color:#fff;box-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075);position:relative;text-align:center}@media screen and (max-width: 800px){#myContact .myContactSection{margin-bottom:70px}}#myContact .myContactSection .myContactSectionIcon{background:red;width:80px;height:80px;border-radius:50%;position:absolute;top:-40px;left:50%;margin-left:-40px;display:flex;justify-content:center;align-items:center;text-align:center;color:#fff;font-size:250%}#myContact .myContactSection .myContactLink{margin-top:70px}#myContact .myContactSection .myContactButtonHolder{margin-top:40px}#myContact .myContactSection .myContactButtonHolder .myContactButton{padding:5px 20px;border-radius:30px;color:#fff;font-size:1.2em;border:none;cursor:pointer;box-shadow:0 0.125rem 0.25rem rgba(0,0,0,0.075);background-color:red}

#myAbout{display:flex;justify-content:center;margin-bottom:70px}#myAbout .myAboutInfoHolder .myAboutQA div{display:inline-block}@media screen and (max-width: 800px){#myAbout .myAboutInfoHolder .myAboutQA div{display:block}}#myAbout .myAboutTitle{width:100px;text-align:right;font-weight:800;font-size:150%;color:#f00}@media screen and (max-width: 800px){#myAbout .myAboutTitle{text-align:left}}#myAbout .myAboutAnswer{margin:0px 0px 10px 10px}@media screen and (max-width: 800px){#myAbout .myAboutAnswer{margin:0}}

